  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background: var(--switcher-gradient);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--white-cl);
  background-position: center;
  background-repeat: no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 21px;
}

input:checked + .slider, input:focus + .slider {
  background: var(--switcher-gradient);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + .slider:before {
  background-color: var(--white-cl);
  background-position: center;
  background-repeat: no-repeat;
  transform: translateX(-20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

